@import "~antd/dist/antd.css";

body {
  margin: 0;
  font-family: "IBM Plex Sans", sans-serif;
}

.main {
  width: 100%;
  height: 100vh;
}

.logo-container {
  align-items: center;
  display: flex;
  justify-content: center;
  margin-top: 20px;
  max-width: 200px;
}

.logo-large {
  width: 130px;
}

.logo-small {
  width: 40px;
  height: 40px;
}

.site-layout-sub-header-background {
  background: #fff;
}

.site-layout-background {
  background: #fff;
}

.ant-radio-group label {
  margin: 5px;
}

.pointer {
  cursor: pointer;
}

.d-flex-row {
  display: flex;
  flex-direction: row;
}

.d-flex-col {
  display: flex;
  flex-direction: column;
}

.margin-zero {
  margin: 0;
}

.ant-checkbox-wrapper {
  margin: 0 !important;
  padding: 5px;
  border-radius: 5px;
}
.ant-checkbox-wrapper:hover {
  background-color: #e6fffb;
}

.ant-tag:focus {
  color: #ffffff;
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary:hover {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}

.ant-btn-primary:focus {
  background-color: rgb(0, 0, 0);
  border-color: rgb(0, 0, 0);
}
